.group-selection {
  position: absolute;
  padding: 9px 14px;
  margin-bottom: 12px;
  background-color: var(--background-header);
  border-radius: 3px;
  box-shadow: 0 4px 20px 0 rgba(#0b141a, .1), 0 8px 17px 0 rgba(#0b141a, 0.16);
  z-index: 10;
  transform: scale(1);
  transition: all 1s;
  .emoji-icon {
    width: 32px;
    height: 32px;
    cursor: pointer;
    margin: 1px;
  }

  :nth-child(1) {
    padding-right: 12px;
    margin-right: 12px;
    width: 43px;
    border-right: 1px solid rgba(var(--msgshadow), 0.2);
  }

  .triangle {
    position: absolute;
    bottom: -7px;
    width: 14px;
    height: 14px;
    background-color: var(--background-header);
    transform: translateY(0%) rotateZ(45deg);
    left: 22px;
    bottom: -8px;
  }
}
