.chat {
  order: 2;
  margin-top: 59px;
  flex: 1 1 0;
  list-style: none;

  &__wrapper {
    height: 100%;
    top: 0;
    padding-right: 6.5%;
    padding-left: 6.5%;
    align-items: flex-start;
    margin-bottom: 12px;
  }

  &__by-me {
    background-color: var(--mymessagebg);
  }

  &__by-other {
    background-color: var(--incomingmessage);
  }

  .message {
    font-size: 14.2px;
    line-height: 19px;
    color: var(--message--primary);
    box-shadow: 0 1px 0.5px rgba(var(--msgshadow), 0.13);
    border-radius: 7.5px;
    user-select: text;
    padding: 6px 7px 8px 9px;
    position: relative;

    &__time {
      color: var(--created-at-msg);
      font-size: 0.6875rem;
    }

    &--right-triangle {
      @include msgArrow(right, var(--mymessagebg));
    }

    &--left-triangle {
      @include msgArrow(left, var(--incomingmessage));
    }

    &__inner {
      overflow-wrap: break-word;
      white-space: pre-wrap;
      max-width: 500px;

      img {
        width: 20px;
        height: 20px;
        image-rendering: -webkit-optimize-contrast;
      }
    }
  }
}
