@mixin msgArrow($dir, $color) {
  position: absolute;
  top: 0;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid $color;
  @if $dir == left {
    left: -8px;
  } @else {
    right: -8px;
  }
}
